<template>
	<div id="app" >
		<!-- TITLE -->
		<div style="font-size: 60px;font-weight: bold;"> Better Call Proof </div>

		<br>
		<!-- Connect Wallet Button -->
		<button v-if="testAddress == undefined"  v-on:click="connectWallet()" > Connect Wallet </button>
		<button v-else  v-on:click="connectWallet()" > {{ shortenHash(this.testAddress)  }} </button>
		<h1> Proof of Existence</h1>
		<p> 
			Proof of ownership is the key to maintaining the authenticity to any of your most important documents. Secure any file on the blockchain following these simple steps.
		</p>
		<h3 > Upload  a file to the ether</h3>

		<p>  Create a permanent record of the
			existance of the file by uploading
			the its hash to the blockchain
		</p>
			<label  @change="uploadFile()">
				<input  type="file"  ref="file">
			</label>
		<br>
		<br><br>

		<div > Document Hash: </div>
		<textarea v-model="doc_to_upload._hash" name="" id="hash-textarea" cols="30" rows="10" placeholder="Hash SHA256"></textarea>
		<div > Optional Message: </div>
		<textarea v-model="doc_to_upload.message" name="" id="" cols="30" rows="10" placeholder="Message to upload"></textarea>
		<div > Estimated Datetime: <span style="font-weight:bold;font-size:medium;margin-left:20px;">{{this.doc_to_upload.est_datetime}}</span> </div>
		<br>
		<button @click="uploadDocData()" >Make Permanent</button>
		<br><br><br><br>

	<div v-if="documents.length > 0" >
		<div >Your Uploaded Documents</div>
		<table>
			<tr>
				<th >#</th>
				<th>Hash (SHA256)</th>
				<th>Message</th>
				<th>Datetime</th>
			</tr>
			<tr v-for="doc, i in this.documents" :key="i">
				<td > {{i}} </td>
				<td> 
				<div >
					<!-- {{shortenHash(doc._hash)}}  -->
					<div >{{doc._hash}}</div>
				</div>
				</td>
				<td> 
				<div >
					<!-- {{doc.message.slice(0,60) }} -->
					<div >{{doc.message}}</div>
				</div>
			</td>
				
				<td> {{epochToHumanReadable(doc.datetime)}} </td>
			</tr>
		
			</table>


			<br><br>

			<div>
				<div >

					<h3 > Verify the existence of documents </h3>
					<p>Just choose any file you need authenticating and upload it. You will a receive a hash which will mean the transaction on the blockchain is complete and authenticity of your file will be secure.
					</p>
							<label  @change="createHashOfFileToVerify()">
							<input  type="file"  ref="file2">
						</label>
					<br><br><br>
				</div>
				<div  >
					<div > Document Hash: </div>
					<textarea v-model="doc_to_verify_hash" name="" id="hash-textarea" cols="30" rows="10" placeholder="Insert Hash to verify or Select a file"></textarea>
					
					<div > Accompanied Message: </div>
					<textarea disabled v-model="this.verified_doc.message" name="" id="" cols="30" rows="10" placeholder="Message"></textarea>
					<br>
					<div > Document was created before: 
						<div v-if="this.verified_doc.error == false" style="color: green">
							<span v-if="this.verified_doc.datetime != 0"> {{epochToHumanReadable(this.verified_doc.datetime)}} ✓</span>
						</div> 
						<div v-else style="color: red"> This File Does not Exist on The Blockchain ❌</div> 
						
					</div>
					<br>
					<button @click="verifyDocumentHash(doc_to_verify_hash)" >Verify Document</button>
					
				</div>
			</div>

			<br><br><br><br>
		</div>
	</div>
</template>

<script>

import Web3 from 'web3'
import abi from "./assets/abi"

import CryptoJS from "crypto-js" // Library for Hash Algorithms (SHA256)

let web3 = new Web3(window.ethereum); // Web3js Library
const contractAddress = "0x4c7f77e11f7e84db3fd6d4b362ef71a221b7e214" // This is the address of the contract on the blockchain

let contract = new web3.eth.Contract(abi, contractAddress) // Smart Contract 

export default {
	name: 'App',
	components: {

	},
	data(){
		// App Variables
		return {
			connected: false, // Wallet Conected/ not  connected
			documents: [],
			doc_to_upload: {
				_hash: "",
				message: "",
				est_datetime: Date.now()
			},
			verified_doc: {
				message: "",
				_hash: "",
				datetime: 0,
				error: false
			},
			doc_to_verify_hash: "",
			testAddress : undefined,
			file_to_upload: ""
		}
	},


	// When the application starts
	async created(){

		this.doc_to_upload.est_datetime = this.epochToHumanReadable(Date.now()/1000)

		// We get the address of the connected user and get their uploaded documents from the blockchain (if any) 
		web3.eth.getAccounts().then((res)=>{
			this.testAddress = res[0]
			this.getDocumentsOf(this.testAddress)
		})

		// Event On Change Account
		window.ethereum.on('accountsChanged',  (accounts) => {
					console.log(accounts[0])
					this.testAddress = accounts[0]
					this.getDocumentsOf(this.testAddress)		
		})
	},


	// Functions of our application
	methods:{
		// Get all documents of a user address
		getDocumentsOf: function(address) {
			contract.methods.getDocumentOf(address).call({from:address})
				.then(res => {
					this.documents = res;
					console.log("Entries() Response: ", res)
				})
		},
		// Connnects website to the metamask extension
		connectWallet : async function(){
					try {
						const addresses = await window.ethereum.request({ method: "eth_requestAccounts" });
						console.log("Wallet Addresses: ", addresses)
						this.testAddress = addresses[0]
						// return  addresses[0]
					} catch (e) {
						console.log("error in request", e);
						// location.reload();
					}
		},
		// Makes Data permanent (Upload to the blockchain)
		uploadDocData: function(){
			contract.methods.addDocument(this.doc_to_upload._hash, this.doc_to_upload.message ).send({from:this.testAddress})
				.then(res => {
					console.log("AddDocument() Response: ", res)
					this.getDocumentsOf(this.testAddress)
				})
		},
		// Calculates Hash of the selected file to upload
		uploadFile: function(){
			this.file_to_upload = this.$refs.file.files[0];
			console.log(this.file_to_upload)
			var reader = new FileReader();
			let that = this
			reader.addEventListener('load',function () {
					var hash = CryptoJS.SHA256(CryptoJS.enc.Latin1.parse(this.result));
					var hash_s = hash.toString(CryptoJS.enc.Hex)
					that.doc_to_upload._hash = hash_s
					var output = hash_s
					console.log(output);
					
				})
			reader.readAsBinaryString(this.file_to_upload);

		},
		// Calculates Hash of the selected file to verify
		createHashOfFileToVerify: function(){
			this.file_to_upload = this.$refs.file2.files[0];
			console.log(this.file_to_upload)
			var reader = new FileReader();
			let that = this
			reader.addEventListener('load',function () {
					var hash = CryptoJS.SHA256(CryptoJS.enc.Latin1.parse(this.result));
					var hash_s = hash.toString(CryptoJS.enc.Hex)
					that.doc_to_verify_hash = hash_s
					var output = hash_s
					console.log(output);
					
				})
			reader.readAsBinaryString(this.file_to_upload);

		},
		// Verify Document button
		verifyDocumentHash: function(hash){
				//Verify document
				console.log("Hash to Verify: " ,hash)
				contract.methods.documents(hash).call({from:this.testAddress})
						.then(res => {
							if (res["datetime"] == 0){
								this.verified_doc  = {error: true }
								return
							}
							res["error"] = false
							this.verified_doc = res
							console.log("Verified Doc:", this.verified_doc , hash )
						})
		},
		// Change from Unix Time to Human Readable Datetime
		epochToHumanReadable: function(epoch){
			let date = new Date(epoch*1000)
			return date.toGMTString()
		},
		shortenHash: function(addr){
			return addr.slice(0,4) + " . . . " + addr.slice(42-4, 42)
		}
	}
}
</script>

<style>
html{
	background-color: bisque;
}
table, th, td {
	/* word-wrap: break-word; */
	border: 1px solid;
	background-color: white;
}

table{
	margin-left: -70px;
}

textarea{
	width: 400px;
	height: 40px;;
}

#app{
	width: 500px;
	margin: auto;
	text-align: center;
	
}

</style>
